html {
    font-size: $small-font-size;
    font-family: $font-family;

    @media screen and (min-width: map-get($breakpoints, xs)) {
        font-size: $base-font-size;
    }

}

h1 {
    font-size: 1.5rem;
    font-weight: bold;
}

h2 {
    font-size: 1.2rem;
}

i {
    font-size: 2.5rem;
}